*{
    font-family: 'Poppins,sans-serif';
    overflow-x: hidden;
}

@media  (min-width: 900px){
    .contact{
        margin: auto;
        margin-top: 40px;
        padding-bottom: 60px;
    }
    
    .contact-icons{
        font-size: 30px;
        padding: 10px;
        margin: auto;
    }
    
    .contact-form{
        display: flex;
        padding: 40px;
        justify-content: center;
        gap: 30px;
    }
    
    .contact-form .input-data{
        height: 45px;
        width: 400px;
        border-radius: 15px;
        padding: 10px;
        border: 2px solid grey;
    
    }
    
    .text-cont .contact-area{
       width: 850px;
       justify-content: center;
       border-radius: 15px;
       background: transparent;
       resize: none;
       border: 2px solid grey;
       padding: 20px;
    }   
    
    
    .contact .submit-btn{
        padding: 10px;
        font-family:'Poppins,sans-serif';
        font-size: 20px;
        font-weight: 300;
        margin: 30px;
        color: #d3a118;
    }
    
}

