*{
    font-family: 'Poppins,sans-serif';
    overflow-x: hidden;
}

#home{
    font-family: "Poppins,Sans-serif";
    margin: auto;
    width: 100%;
    background-color: #1A202C;
    height: 500px;
    overflow-x: hidden;
}

#home .content{
    /* position: absolute; */
    justify-content: center;
    text-align: center;
    border-radius: 15px;
    margin: auto;
    padding: 10%;
    width: 100%;
    /* z-index: 2; */
}

#about{
    /* background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 100%, rgba(0,212,255,1) 100%, rgba(1,187,239,1) 100%); */
    background-color:  #1A202C;
    width: 100%;
    overflow-x: hidden;
}

#projects{
    /* background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 100%, rgba(0,212,255,1) 100%, rgba(1,187,239,1) 100%); */
    background-color:  #1A202C;
    width: 100%;
    margin: auto;

}

#contact{
    background-color:  #1A202C;
    width: 100%;
    margin: auto;
}

.head-image{
    width: 250px;
    height: 250px;
    margin: auto;
}
  
#stat{
    background-color:  #1A202C;
    width: 100%;
}
#skills{
    width: 100%;
    margin: auto;
    /* background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 100%, rgba(0,212,255,1) 100%, rgba(1,187,239,1) 100%); */
    background-color:  #1A202C;
}

@media  (min-width: 900px){
    #home{
        font-family: "Poppins,Sans-serif";
        margin: auto;
        width: 100%;
        height: 500px;
        overflow-x: hidden;
    }

    .name{
        font-size:80px; 
        font-weight:700; 
        line-height:100px; 
        color:#FFF;
    }
    
    
    #home .content{
        justify-content: center;
        text-align: center;
        vertical-align: middle;
        margin: auto;
        width: 100%;
    }

    .dev{
        justify-content: center;
        font-size: 32px;
        line-height: 45px;
        font-weight: 600;
        font-family: "Poppins,Sans-serif";
    }
}

@media  (min-width: 600px)  and (max-width: 899px) {
    #home{
        font-family: "Poppins,Sans-serif";
        margin: auto;
        width: 100%;
        height: 450px;
        overflow-x: hidden;
    }

    .name{
        font-size:64px; 
        font-weight:700; 
        line-height:80px; 
        color:#FFF;
    }
    
    
    #home .content{
        justify-content: center;
        text-align: center;
        border-radius: 15px;
        margin: auto;
        width: 100%;
    }

    .dev{
        justify-content: center;
        font-size: 28px;
        line-height: 40px;
        font-weight: 600;
        font-family: "Poppins,Sans-serif";
    }

    
}
@media (max-width: 599px) {
    #home{
        font-family: "Poppins,Sans-serif";
        margin: auto;
        width: 100%;
        height: 450px;
        overflow-x: hidden;
        margin-bottom: 40px;
    }

    .name{
        font-size:40px; 
        font-weight:600; 
        line-height:54px; 
        padding-top: 60px;
        color:#FFF;
    }
    
    
    #home .content{
        justify-content: center;
        text-align: center;
        border-radius: 15px;
        margin: auto;
        padding: 5%;
        padding-top: 20%;
        width: 100%;
    }

    .dev{
        justify-content: center;
        font-size: 20px;
        line-height: 36px;
        font-weight: 400;
        font-family: "Poppins,Sans-serif";
        padding-bottom: 40px;
    }
}