*{
    font-family: 'Poppins,sans-serif';
    overflow-x: hidden;
}
.project-cont{
    display: grid;
    grid-template-columns:repeat(auto-fill,minmax(400px,max-content));
    grid-template-rows: repeat(auto-fill);
    margin: auto;
    justify-content: center;
    grid-gap: 40px 40px;
    padding-bottom: 30px;
}

.Project-item{
    /* border: 1px solid red; */
    /* box-shadow: 0 0 10px 10px #f4f6f8; */
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
    height: 480px;
    width: 380px;
    border-radius: 10px;
    cursor: pointer;
    /* border: 1px solid white; */
    transition-duration: 0.20s;
    transition-delay: 0.15s;
    background-color: #282c3b;
    padding: 10px;
}
.Project-item:hover{
    transition-duration: 0.20s;
    transition-delay: 0.15s;
    box-shadow: 0 0 5px 10px #ffffff;
    transition: var(--transition) ;
    transition-duration: 100ms;
    transform: translateY(1rem);
    color: #ffffff;
    width: 390px;
}
.Project-image{
    margin: auto;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
    width: 95%;
    /* border: 1px solid green; */
}
.project-title{
    font-size: 22px;
    font-weight: 700;
    line-height: 28px;
    font-family:'Poppins,sans-serif';
    margin-top: 8px;
}
.project-description{
    margin: 5px;
    text-align: left;
    font-size: 14px;
    font-weight: 300;
    /* border: 1px solid red; */
    line-height: 24px;
    margin-bottom: 10px;
}

.project-tech{
    text-align: left;
    font-size: 14px;
    margin: 5px;
    font-weight: 300;
    line-height: 24px;
    /* border: 1px solid red; */
}

.Project_btn{
    justify-content: center;
    gap: 10px;
    display: flex;
}
.primary{
    background-color: #90A0D9;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}

@media (max-width: 599px) {
    .project-cont{
        margin: auto;
        justify-content: center;
        gap: 20px;
        padding-bottom: 10px;
    }
    
    .Project-item{
        box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
        height: 450px;
        width: 300px;
        border-radius: 10px;
        cursor: pointer;
        background-color: #282c3b;
        padding: 5px;
        margin: auto;
        padding-top: 8px;
    }
    .Project-item:hover{
        box-shadow: 0 0 10px 10px #ffffff;
        transition: var(--transition) ;
        transition-duration: 100ms;
        transform: translateY(0.5rem);
        color: #ffffff;
        width: 310px;
    }
    .Project-image{
        margin: auto;
        box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
        width: 98%;
        /* border: 1px solid green; */
    }
    .project-title{
        font-size: 16px;
        line-height: 24px;
        text-decoration: underline;
        margin-top: 8px;
    }
    .project-description{
        margin: 5px;
        text-align: left;
        font-size: 12px;
        font-weight: 300;
        line-height: 18px;
        margin-bottom: 10px;
    }
    
    .project-tech{
        text-align: left;
        font-size: 12px;
        margin: 5px;
        font-weight: 300;
        line-height: 18px;
    }
    
    .Project_btn{
        margin-top: 10px;
        justify-content: center;
        gap: 10px;
        display: flex;
    }
    .primary{
        background-color: #272c3d;
        box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
    }
}