*{
    font-family: 'Poppins,sans-serif';
}

  
@media  (min-width: 900px){
    .about{
        display: flex;
        width: 100%;
        margin: auto;
        text-align: center;
        height: auto;
        font-family: "Poppins,Sans-serif";
        gap: 20px;
        padding-bottom: 30px;
    }

    .img-cont{
        margin: auto;
    }
    
    .bio-cont{
        text-align: left;
        /* border: 2px solid red; */
        margin: auto;
        color: #D6D6D6;
        font-size: 18px;
        font-family: "Poppins,Sans-serif";
        width: 60%;
        font-weight: 300;
        line-height: 28px;
    }
    
    .dummy{
        width: 300px;
        height: 390px;
        margin: auto;
        border-radius: 10px;
        margin-bottom: 30px;
        box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
        cursor: pointer;
    }
    
}

@media  (min-width: 600px)  and (max-width: 899px) {
    .about{
        display: flex;
        width: 100%;
        margin: auto;
        text-align: left;
        height: auto;
        font-family: "Poppins,Sans-serif";
        gap: 10px;
        padding-bottom: 30px;
    }

    .img-cont{
        margin: auto;
    }
    
    .bio-cont{
        text-align: left;
        margin: auto;
        color: #D6D6D6;
        font-size: 14px;
        font-family: "Poppins,Sans-serif";
        width: 65%;
        font-weight: 300;
        line-height: 24px;
    }
    
    .dummy{
        width: 200px;
        height: 270px;
        margin: auto;
        border-radius: 10px;
        margin-bottom: 30px;
        box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
        cursor: pointer;
    }
    
}
@media (max-width: 599px) {
    .about{
        width: 100%;
        margin: auto;
        text-align: left;
        height: auto;
        font-family: "Poppins,Sans-serif";
        gap: 10px;
        padding-bottom: 30px;
    }

    .img-cont{
        margin: auto;
    }
    
    .bio-cont{
        text-align: left;
        margin: auto;
        color: #D6D6D6;
        font-size: 14px;
        font-family: "Poppins,Sans-serif";
        width: 95%;
        font-weight: 300;
        line-height: 24px;
    }
    
    .dummy{
        width: 190px;
        height: 245px;
        margin: auto;
        border-radius: 10px;
        margin-bottom: 30px;
        box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
        cursor: pointer;
    }
}