*{
    font-family: 'Poppins,sans-serif';
    overflow-x: hidden;
}

#data{
    width: 100%;
    margin: auto;
    display: block;
    z-index: 2;
}


.bread:hover{
  color: #90A0D9;
}

@media  (min-width: 900px){
    #nav-menu{
        position: sticky;
        top: 0;
        z-index: 100;    
        width: 100%;
        margin: auto;
        font-family: 'Poppins';
        /* box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px; */
        display: block;
        padding-top: 20px;
        -webkit-backdrop-filter: blur(8px);
        backdrop-filter: blur(10px);
    }
    
    
    #nav-menu .nav-list{
      justify-content:right;
      padding: 20px;
      
    }
    
    .items{
        gap: 40px;
        width: 100%;
        justify-content: right;
    }
    .bread{
      font-size: 20px;
      color: #d3a118;
      font-weight: 600;
    }
}

@media  (min-width: 600px)  and (max-width: 899px) {
    #nav-menu{
        position: sticky;
        top: 0;
        z-index: 100;    
        width: 100%;
        margin: auto;
        display: block;
        padding-top: 15px;
        -webkit-backdrop-filter: blur(8px);
        backdrop-filter: blur(8px);
    }
    
    #nav-menu .nav-list{
      /* justify-content: center; */
      padding:20px 0px 20px 0px;
    }
    
    .items{
      width: 100%;
        gap: 10px;
        
    }

    .bread{
      font-size: 18px;
      font-weight: 400;
      margin: auto;
    }
}
@media (max-width: 599px) {
    #nav-menu{
        position: fixed;
        bottom: 0px;
        z-index: 2;    
        width: 100%;
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
        margin: auto;
        display: block;
        padding-top: 5px;
        padding-bottom: 15px;
        border-top: 1px solid grey;
        -webkit-backdrop-filter: blur(12px);
        backdrop-filter: blur(12px);
    }
    
   .items>.theme{
      display: none;
      visibility: hidden;
      /* font-size: 40px; */
      border: 1px solid red;
    }
    
    #nav-menu .nav-list{
      padding: 10px 0px 10px 0px;
    }
    
    .items{
      width: 100%;
      justify-content: space-evenly;
    }

    .bread{
      font-size: 14px;
      font-weight: 400;
      margin: auto;
      /* border: 1px solid red; */
    }
}