*{
    font-family: 'Poppins,sans-serif';
    overflow-x: hidden;
}

@media  (min-width: 900px){
    #skills-container{
        width: 100%;
        margin: auto;
        justify-content: center;
        align-items: center;
    }

    .grid-skills{
    display: grid;
    grid-template-columns:repeat(auto-fill,minmax(170px,max-content));
    grid-template-rows: repeat(auto-fill);
    margin: auto;
    justify-content: center;
    grid-gap: 30px 40px;
    }
    
    .tools-box .skill-logo{
        margin: auto;
        width: 100px;
        height: 100px;
        margin-bottom: 5px;
    }
    
    .tools-box{
        width: 120px;
        margin-bottom: 70px;
        height: 150px;
        /* box-shadow: 0 0 10px 10px #d3a118; */
        box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;        padding-top: 15px;
        color: #d3a118;
        border-radius: 10px;
        align-items: center;
        color: #fff;
        cursor: pointer;
        transition-duration: 0.20s;
        transition-delay: 0.15s;
        background-color: black;
        /* font-size: 16px; */
    }
    
    .tools-box:hover{
        box-shadow: 0 0 10px 5px #ecf1f5;;
        width: 130px;
        height: 160px;
        margin-bottom: 50px;
        transition-duration: 0.20s;
        transition-delay: 0.15s;
        transition: var(--transition) ;
        /* transform: matrix3d(1rem); */
        transform: translateY(-1rem);
        /* background-color: white; */
        color: #fff;
    }
}

@media  (min-width: 600px)  and (max-width: 899px) {
    #skills-container{
        width: 100%;
        margin: auto;
        justify-content: center;
        align-items: center;
    }
    
    .tools-box .skill-logo{
        margin: auto;
        width: 70px;
        height: 70px;
        margin-bottom: 5px;
    }
    .grid-skills{
        display: grid;
        grid-template-columns:repeat(auto-fill,minmax(120px,max-content));
        grid-template-rows: repeat(auto-fill);
        margin: auto;
        justify-content: center;
        grid-gap: 20px 30px;
        }

    .tools-box{
        width: 100px;
        margin-bottom: 70px;
        height: 120px;
        box-shadow: 0 0 10px 10px #d3a118;
        /* box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px; */
        justify-content: center;
        padding-top: 15px;
        color: #d3a118;
        border-radius: 10px;
        align-items: center;
        color: #fff;
        cursor: pointer;
        background-color: black;
        /* font-size: 16px; */
    }
    
    .tools-box:hover{
        box-shadow: 0 0 20px 20px #ffffff;
        width: 130px;
        height: 160px;
        margin-bottom: 50px;
        transition-duration: 100ms;
        transition-delay: 50ms;
        transition: var(--transition) ;
        /* transform: matrix3d(1rem); */
        transform: translateY(-1rem);
        /* background-color: white; */
        color: #fff;
    }
}
@media (max-width: 599px) {
    #skills-container{
        width: 100%;
        margin: auto;
        justify-content: center;
        align-items: center;
    }
    
    .tools-box .skill-logo{
        margin: auto;
        width: 40px;
        height: 40px;
        margin-bottom: 10px;
    }
    .grid-skills{
        display: grid;
        grid-template-columns:repeat(auto-fill,minmax(90px,max-content));
        grid-template-rows: repeat(auto-fill);
        margin: auto;
        justify-content: center;
        grid-gap: 15px 15px;
        }

    .tools-box{
        width: 80px;
        margin-bottom: 50px;
        height: 90px;
        box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
        justify-content: center;
        padding-top: 10px;
        color: #d3a118;
        border-radius: 10px;
        align-items: center;
        font-size: 12px;
        color: #fff;
        cursor: pointer;
        background-color: black;
        /* font-size: 16px; */
    }
    
    .tools-box:hover{
        box-shadow: 0 0 10px 10px #d3a118;
        width: 85px;
        height: 95px;
        margin-bottom: 50px;
        transition-duration: 100ms;
        transition-delay: 50ms;
        transition: var(--transition) ;
        transform: translateY(-1rem);
        color: #d3a118;
    }
}